import "./ListItem.css";
import {calculatePriceOfProduct} from "../../services/utils";

const ListElement = ({changeAmount, item, deleteItem}) => {
	
	return (
		<div className="product_list__element">
			<div className={"product_list__element__inputs"}><b>{item.name.toUpperCase()}</b>,  : <span
				className="span">_</span>
				<input type="number"
				       min="0"
				       step="0.01"
				       name={item.name}
				       value={item.pack}
				       onChange={(e) => {
					       changeAmount(e, "pack");
				       }}/>
			</div>
			<div className={"product_list__element__inputs"}>
				  ({item.metric}):<span className="span">_</span>
				<input
					step="0.01"
					type="number"
					onChange={(e) => {
						changeAmount(e, "amount");
					}}
					value={item.amount}
					name={item.name}>
				</input>
			</div>
			<div className={"product_list__element__inputs"}>
				  : <span className="span">_</span>
				<input
					step="0.01"
					type="number"
					value={item.price}
					onChange={(e) => {
						changeAmount(e, "price");
					}}
					name={item.name}/>
			</div>
			<div className={"product_list__element__inputs"}>  :
				<b> {
					Number.isNaN(calculatePriceOfProduct(item.price, item.amount, item.pack)) ?
						"  !" : calculatePriceOfProduct(item.price, item.amount, item.pack).toFixed(2) + " "
				}</b>
			</div>
			<div className={"product_list__element__buttons"}>
				<button
					className={"product_list__element__buttons__button"}
					name={item.name}
					onClick={() => deleteItem(item.id)}>
					
				</button>
			</div>
		
		</div>
	);
};

export default ListElement;