import "./Header.css";
import {NavLink} from "react-router-dom";

import React from 'react';

const Header = () => {
    return (
        <div className={"header"}>
            <h1 className={"header__name"}>  </h1>
            <nav className="header__navbar">
                <NavLink className="header__navbar_link" to="new-item">  </NavLink>
                <NavLink className="header__navbar_link" to="/"> </NavLink>
                <NavLink className="header__navbar_link" to="package"> </NavLink>
                <NavLink className="header__navbar_link" to={"delete-item"}> </NavLink>
            </nav>
        </div>
    );
};

export default Header;